// @ts-nocheck
import { useCallback } from "react";

export function useCombinedRefs<T>(...refs: React.Ref<T>[]) {
  return useCallback(
    (handle: T | null) => {
      for (const ref of refs) {
        if (typeof ref === "function") {
          ref(handle);
        } else if (ref !== null) {
          ref.current = handle;
        }
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    refs
  );
}
